
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import SidebarMenu from '@/components/SidebarMenu.vue'
import useFetching, { FetchStatus } from '@/hooks/useFetching'

export default defineComponent({
  setup () {
    const router = useRouter()
    const store = useStore()
    const rail = ref<boolean>(false)

    const handleLogout = async () => {
      await store.dispatch('user/logout')
      await router.push('/login')
    }

    const [loadCommon, status] = useFetching(() => {
      store.dispatch('user/loadUser')
      store.dispatch('category/loadTree')
      store.dispatch('attribute/loadGrouped')
      store.dispatch('filter/loadGrouped')
      // store.dispatch('productStatus/loadItems')
      store.dispatch('weightClass/loadItems')
      store.dispatch('lengthClass/loadItems')
      store.dispatch('manufacturer/loadItems')
      store.dispatch('orderStatus/loadItems')
      store.dispatch('returnAction/loadItems')
      store.dispatch('returnReason/loadItems')
      store.dispatch('returnStatus/loadItems')
      store.dispatch('customerGroup/loadItems')
      store.dispatch('customer/loadItems')
      store.dispatch('voucher/loadItems')
      store.dispatch('voucherTheme/loadItems')
      // store.dispatch('country/loadItems')
      // store.dispatch('zone/loadItems')

      return store.dispatch('option/loadTypes')
    })

    return {
      rail,
      handleLogout,
      loadCommon,
      status
    }
  },

  components: {
    SidebarMenu
  },

  computed: {
    user () {
      return this.$store.getters['user/getUser']
    },

    isLoading (): boolean {
      return this.status === FetchStatus.pending
    }
  },

  beforeMount () {
    this.loadCommon()
  }
})
